.hell-wrap{position:relative; height:100vh;}
.lock{overflow:hidden;}

.view{visibility:visible; opacity:1;}
.hide{visibility:hidden; opacity:0; transition:all .2s;}

.guide{position:absolute; top:0; left:0; z-index:10; width:100%; height:100%; background:rgba(0, 0, 0, .7);}
.guide-inner{position:absolute; top:50%; left:50%; transform:translate(-50%, -50%);}
.guide h2{padding:0 0 40px; font-size:50px; font-weight:700; text-align:center; color:#c44c1f;}
.guide-content{display:flex; justify-content:center; align-items:center; width:1200px; margin:0 auto;}
.guide-content .img{flex:1;}
.guide-content .img img{width:420px; margin:8px 0;}
.guide-content ul{flex:2; margin-left:50px; counter-reset:num 0;}
.guide-content li{padding:0 0 0 40px; font-size:23px; color:#fff; text-indent:-40px; white-space:nowrap;}
.guide-content li:not(:first-child){margin:17px 0 0 0;}
.guide-content li:before{counter-increment:num 1; content:counter(num); display:inline-block; width:30px; height:30px; margin:0 10px 0 0; font-size:16px; font-weight:500; color:#fff; vertical-align:top; text-indent:0; text-align:center; line-height:28px; border-radius:50%; background:#000;}
.guide-content li div{text-indent:0; line-height:1.4;}
.guide-close{position:absolute; top:20px; right:20px; display:flex; align-items:center;}
.guide-close .checkbox{position:relative; margin:0 20px 0 0;}
.guide-close .checkbox label{position:relative; display:block; padding:0 0 0 25px; color:#fff;}
.guide-close .checkbox label:before{position:absolute; top:1px; left:0; width:18px; height:18px; content:''; background:#fff;}
.guide-close .checkbox label:after{position:absolute; top:2px; left:5px; display:none; content:''; width:5px; height:10px; border-right:3px solid #c44c1f; border-bottom:3px solid #c44c1f; transform:rotate(45deg);}
.guide-close .checkbox input[type="checkbox"]{position:absolute; top:0; left:0;}
.guide-close .checkbox input[type="checkbox"]:checked + label:after{display:block;}
.btn-guideClose{position:relative; appearance:none; width:30px; height:30px; overflow:hidden; padding:0; text-indent:-9999px; border:0; background:none;}
.btn-guideClose:before{position:absolute; top:-2px; left:13px; width:3px; height:33px; content:''; background:#fff; transform:rotate(45deg);}
.btn-guideClose:after{position:absolute; top:-2px; left:13px; width:3px; height:33px; content:''; background:#fff; transform:rotate(-45deg);}

/* header */
.hell-wrap .aside{position:fixed; top:0; left:0; z-index:2; width:100px; height:100%; box-sizing:border-box; box-shadow:0 0 12px rgba(0, 0, 0, .3);}
.hell-wrap .logo{float:left; padding:10px;}
.hell-wrap .logo img{width:170px;}
.hell-wrap .btn-close{position:relative; display:block; width:100%; height:100px; overflow:hidden; font-size:35px; text-indent:-9999px; color:#fff; border:0; background:#c33 url(/public/images/hell/icon_close_all.png) no-repeat center center;}
.hell-wrap .btn-close span{position:absolute; top:-100%; left:-100%;}
.hell-wrap .btn-reset{display:block; width:80%; margin:30px auto 0; font-size:14px; color:#222; border:0; background:none;}
.hell-wrap .btn-reset span{display:block;}
.hell-wrap .sbj{display:block; margin:0 0 10px; font-size:12px; text-align:center;}
.hell-wrap .paper{margin:30px 0 0;}
.hell-wrap .paper button{width:100%; margin:0 0 5px; border:0; background:none;}
.hell-wrap .paper button.active{color:#c33;}
.hell-wrap .util{position:absolute; bottom:50px; left:0; width:100%; text-align:center;}
.hell-wrap .sheet-btn{width:80%; margin:0 auto 20px; font-size:20px;}
.hell-wrap .sheet-btn span{display:block; margin:0 0 10px; font-size:14px;}
.hell-wrap .sheet-btn button{width:100%; height:45px; margin:0 0 10px; font-size:16px; color:#fff; border:0; border-radius:5px; background:#86869b; transition:background .2s;}
.hell-wrap .sheet-btn button.active{background:#c44c1f;}
.hell-wrap .bad{width:100%; box-sizing:border-box;}
.hell-wrap .bad p{display:flex; align-items:center; justify-content:center; margin:6px 0 0; font-size:20px;}
.hell-wrap .bad img{width:30px; margin:0 5px 0 0;}

/* page */
.hell-wrap .content{position:relative; display:flex; justify-content:center; align-items:center; height:100vh;}
.page{position:relative; display:inline-block; margin:0 auto; padding:0 0 0 100px;}
.page img{height:99vh;}
.page .btn-page{margin:0; padding:0; border:0; background:none;}
.btn-area{position:absolute; bottom:2.7vh; right:6.3vh; width:18.56vh;}
.btn-area .btn-page{width:18.56vh; height:3.2vh; overflow:hidden; margin:0.56vh 0 0; text-indent:-9999px;}
.btn-area.full-area{bottom:0; right:0; width:calc(100% - 100px); height:100%;}
.btn-area.full-area .btn-page{width:100%; height:100%; margin:0;}

.sheet{position:fixed; top:0; left:0; width:100%; height:100%; background:rgba(31, 31, 71, .08);}
.sheet-inner{position:absolute; top:20%; left:30%; z-index:10;}
.sheet-inner img{display:block; width:39.58vh; box-shadow:20px 20px 70px 0 rgba(0, 0, 0, .2); cursor:move;}
.sheet .btn-wrap{position:absolute; top:50px; left:0; width:100%; box-sizing:border-box;  overflow:hidden; padding:0 0 0 100px; text-align:center;}
.sheet button{width:96px; height:96px; overflow:hidden; text-indent:-9999px; color:#fff; border:0; border-radius:50%;}
.sheet .btn-sheet-close{margin:0 20px 0 0; background:#c44c1f url(/public/images/hell/btn_sheetClose.png) no-repeat center center;}
.sheet .btn-rotate{background:#1f1f48; background:#1f1f48 url(/public/images/hell/btn_rotate.png) no-repeat center center;}
.sheet .landscape .btn-wrap{right:-170px;}

@media all and (max-width:1280px){
    .guide-inner{width:100%;}
    .guide-content{width:80%;}
    .guide-content .img img{width:100%;}
    .guide-content ul{flex:1;}
    .guide-content li{font-size:20px; white-space:unset;}
}

/* mobile */
@media all and (max-width:1024px){
    .page img{width:100%; height:auto;}
    .btn-area{bottom:25px; right:60px; width:176px;}
    .btn-area .btn-page{width:176px; height:29px; margin:6px 0 0;}
    .sheet-inner img{width:370px;}
    .sheet .btn-wrap{right:-160px;}
}
@media all and (max-width:768px){
    .guide-inner{position:static; padding:60px 0 20px; transform:none;}
    .guide h2{font-size:35px;}
    .guide-content{display:block;}
    .guide-content .img img{width:48%; margin:0 1%;}
    .guide-content ul{margin:20px 0 0;}
    .guide-content li{padding:0 0 0 30px; font-size:15px; text-indent:-30px;}
    .guide-content li:not(:first-child){margin:12px 0 0;}
    .guide-content li:before{width:20px; height:20px; font-size:10px; line-height:18px;}
    .guide-content li img{width:16px; vertical-align:middle;}

    .hell-wrap .aside{display:flex; justify-content:space-between; align-items:center; width:100%; height:60px; background:#fff;}
    .hell-wrap .aside > div{display:flex;}
    .hell-wrap .btn-close{width:60px; height:60px; background:#c33 url(/public/images/hell/icon_close_all.png) no-repeat center center; background-size:18px auto}
    .hell-wrap .btn-reset{width:60px; margin:0; font-size:11px;}
    .hell-wrap .util{position:static; width:auto;}
    .hell-wrap .sheet-btn{display:flex; align-items:center; width:auto; margin:0;}
    .hell-wrap .sheet-btn span{display:inline-block; width:auto; margin:0;}
    .hell-wrap .sheet-btn button{width:50px; margin:0 0 0 5px;}
    .hell-wrap .sbj{margin:0;}
    .hell-wrap .bad{width:50px; margin:0 20px;}
    .hell-wrap .bad p{margin:2px 0 0;}

    .sheet-inner{top:37.3698vw; left:20.4427vw;}
    .sheet-inner img{width:57.2917vw;}
    .sheet .btn-wrap{top:80px; padding:0;}
    .sheet button{width:12.5vw; height:12.5vw;}
    .sheet .btn-sheet-close{margin:0 2.6042vw 0 0; background-size:35% auto;}
    .sheet .btn-rotate{background-size:40% auto;}

    .hell-wrap .content{align-items:baseline;}
    .page{width:100%; padding:60px 0 0;}
    .page img{width:100%; height:auto;}
    .btn-area{bottom:4.1667vw; right:9.375vw; width:27.6042vw;}
    .btn-area .btn-page{width:27.6042vw; height:4.5573vw; margin:0.5208vw 0 0;}
    .btn-area.full-area{width:100%;}
}
@media all and (max-width:380px){
    .hell-wrap .sheet-btn .sbj{display:none;}
    .hell-wrap .sheet-btn button{width:35px;}
    .hell-wrap .bad{margin:0 10px;}
}