#root{height:100%;}
.main-header{position:absolute; top:0; left:0; z-index:2; display:flex; justify-content:space-between; align-items:center; width:100%; height:120px; box-sizing:border-box; padding:0 60px;}
.main-header h1{width:180px;}
.btn-nav{width:64px; height:64px; overflow:hidden; text-indent:-9999px; border:0; border-radius:50%; background:#1f1f48;}
.btn-nav:before{position:absolute; top:25px; left:19px; width:27px; height:3px; content:''; border-radius:2px; background:#fff; transition:all .2s;}
.btn-nav:after{position:absolute; bottom:25px; left:19px; width:27px; height:3px; content:''; border-radius:2px; background:#fff; transition:all .2s;}
.btn-nav.active:before{top:30px; left:14px; width:36px; transform:rotate(45deg);}
.btn-nav.active:after{top:30px; left:14px; width:36px; transform:rotate(-45deg);}

.page-title{position:absolute; bottom:60px; left:105px; z-index:55; font-weight:700; font-family:'Montserrat'; color:#2f2f3b;}

.nav{position:fixed; top:0; right:0; z-index:100; display:none; width:100%; height:100%; text-align:center; background:#1f1f48;}
.nav ul{position:absolute; top:50%; left:50%; width:90%; transform:translate(-50%, -50%);}
.nav li{float:left; width:33.33%; font-size:56px; font-family:'Montserrat'; font-weight:700; text-align:center;}
.nav a{color:#fff; transition:color .2s;}
.nav li.active a,
.nav a:hover{color:#fac24b;}

.contents{display:flex; align-items:center; height:100vh;}
.book-list{position:relative; left:50%; width:100%; transform:translateX(-50%);}
.book-list .swiper-slide{text-align:center;}
.book-list img{width:100%; max-width:550px;}
.book-item{position:relative; width:550px;}
.book-item.swiper-slide-active{filter:blur(0);}
